import React from 'react';

import Table from '../../../../components/Table';
import { samplesTableColumns, sampleIsValid } from '../../../../models/sample';

const SamplesTable = ({ samples, onSampleAdd, onSampleDelete, onSampleUpdate }) =>
  <Table
    title={`Amostras (${samples.length})`}
    data={samples}
    columns={samplesTableColumns}
    editable={{
      onRowAdd: newSample =>
        new Promise((resolve, reject) => {
          if (sampleIsValid(newSample)) {
            onSampleAdd(newSample);

            resolve();
          }
          else {
            reject();
          }
        }),
      onRowUpdate: updatedSample =>
        new Promise((resolve, reject) => {
          if (sampleIsValid(updatedSample)) {
            onSampleUpdate(updatedSample);

            resolve();
          }
          else {
            reject();
          }
        }),
      onRowDelete: deletedSample =>
        new Promise(resolve => {
          onSampleDelete(deletedSample)

          resolve();
        })
    }}
    options={{
      addRowPosition: 'first',
      paging: true,
      pageSize: 10,
      emptyRowsWhenPaging: false,
      showEmptyDataSourceMessage: false,
      pageSizeOptions: [5, 10, 25, 50],
      rowStyle: {
        fontSize: '14px'
      }
    }}
  />

export default SamplesTable;
