import React from 'react';

import {
  Dialog,
  DialogTitle,
  DialogContent,
  Button,
  DialogContentText,
  DialogActions,
} from '@material-ui/core';

import { Alert } from '@material-ui/lab'

const BatchesUploadModal = () => {
  const initialState = {
    open: false,
    title: '',
    text: '',
    error: '',
    content: null
  }

  const [confirmationModal, setConfirmationModal] = React.useState(initialState)

  const openAlertModal = ({ text, title, error, content } = {}) => {
    setConfirmationModal({
      text,
      title,
      error,
      content,
      open: true
    })
  }

  const closeAlertModal = () => {
    setConfirmationModal(initialState)
  }

  window.openAlertModal = openAlertModal

  return (
    <Dialog
      open={confirmationModal.open}
      disableBackdropClick
      fullWidth
    >
      {confirmationModal.title &&
        <DialogTitle>
          {confirmationModal.title}
        </DialogTitle>
      }

      <DialogContent>
        {confirmationModal.text &&
          <DialogContentText>
            {confirmationModal.text}
          </DialogContentText>
        }

        {confirmationModal.error &&
          <Alert severity='error'>
            Erro! {confirmationModal.error}
          </Alert>
        }

        {
          confirmationModal.content &&
          confirmationModal.content()
        }

        <DialogActions >
          <Button
            color='primary'
            variant='contained'
            onClick={closeAlertModal}
          >
            Confirmar
          </Button>
        </DialogActions>
      </DialogContent>
    </Dialog>
  )
}

export default BatchesUploadModal;
