import React from 'react';
import {
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  Button,
  DialogContentText,
  Typography,
} from '@material-ui/core';

import { Alert } from '@material-ui/lab'

import Dropzone from '../../../../components/Dropzone';
import LoadingWrapper from '../../../../components/LoadingWrapper';

import { parseFile, acceptedMimeTypes, acceptedFormats } from '../../../../models/batchUpload';

import { INSTRUCTIONS } from './constants';

const BatchesUploadModal = ({ open, onClose, onSubmit }) => {
  const [error, setError] = React.useState(null);
  const [data, setData] = React.useState([]);
  const [loading, setLoading] = React.useState(false)

  const handleFileDrop = async (file) => {
    setLoading(true);

    try {
      const parsedData = await parseFile(file);

      setData(parsedData);
      setError(null);
    }
    catch ({ message }) {
      setError(message)
    }

    setLoading(false);
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    onSubmit(data);
  }

  return (
    <Dialog
      open={open}
      onClose={onClose}
      disableBackdropClick
      maxWidth='md'
    >
      <LoadingWrapper loading={loading}>
        <DialogTitle>
          <Box display="flex" justifyContent="space-between">
            Adicionar Amostras
          </Box>
        </DialogTitle>

        <DialogContent>
          <Typography>Instruções:</Typography>

          <ul>
            {INSTRUCTIONS.map((instruction, index) =>
              <li key={index}>
                <Typography>{instruction}</Typography>
              </li>
            )}
          </ul>

          <Box textAlign="center">
            <Button
              color='primary'
              variant='outlined'
              onClick={() => window.open('/s-dimorph/tabela-amostras-exemplo.csv')}
            >
              Baixar Modelo
            </Button>
          </Box>

          <DialogContentText>
            Formatos aceitos: {acceptedFormats.join(', ')}
          </DialogContentText>
          <Dropzone
            onChange={handleFileDrop}
            acceptedMimeTypes={acceptedMimeTypes}
          />

          {!!error &&
            <Box marginTop="16px">
              <Alert severity='error'>
                Erro! {error}
              </Alert>
            </Box>
          }
          <Box marginY="16px" display="flex" justifyContent="flex-end">
            <Box marginRight="16px">
              <Button
                color='primary'
                onClick={onClose}
              >
                Cancelar
              </Button>
            </Box>

            <Button
              color='primary'
              variant='contained'
              onClick={handleSubmit}
              disabled={!!error || !data.length}
            >
              Confirmar
            </Button>
          </Box>
        </DialogContent>
      </LoadingWrapper>
    </Dialog>
  )
}

export default BatchesUploadModal;
